.myButton {
 background-color: #131313;
 background-image: -webkit-linear-gradient(283deg, rgba(255, 255, 255, 0.1) 50%, transparent 55%),-webkit-linear-gradient(top, rgba(255, 255, 255, 0.15), transparent);
 border-radius: 6px;
 /* box-shadow: 0 0 0 1px #000000 inset,0 0 0 1px rgba(255, 255, 255, 0.15) inset,0 2px 0 0      #000000,0 2px 0 1px rgba(0, 0, 0, 0.4),0 2px 2px 1px rgba(0, 0, 0, 0.5); */
 color: white;
 display: block;
 font-size: 18px;
 font-weight: 500;
 padding-top: 8px;
 padding-bottom: 8px;
 padding-left: 20px;
 padding-right: 20px;
 margin: 5px;
 position: relative;
 text-align: center;
 text-shadow: 0 1px 1px rgba(0, 0, 0, 0.5);
 text-decoration: none !important;
 -webkit-transition: all .2s linear;
   
}

.myButton:hover {
    background-color: #ffc701;
    background-image: -webkit-linear-gradient(283deg, rgba(255, 255, 255, 0.4) 50%, transparent 55%),-webkit-linear-gradient(top, rgba(255, 255, 255, 0.75), transparent);
    border-style: none;
    color: black;
    poisiton: relative;
    top: -1px;
    box-shadow: 0 0 0 0px #000000 inset,0 0 0 3px rgba(255, 255, 255, 0.15) inset,0 1px 0 0      #000000,0 1px 0 1px rgba(155, 102, 3, 0.4),0 6px 6px 1px rgba(0, 0, 0, 0.3);

}


